.start-menu {
    display: flex;
    position: absolute;
    left: 0;
    bottom: 100%;
    height: 400px;
    width: 17%;
    background-color: #b8b8b8;
    border-top: solid black 1px;
    border-right: solid black 1px;
    border-bottom: solid black 1px;
    border-top-right-radius: 5px;
  }
  
  .start-title-container {
    display: flex;
    width: 18%;
    height: 100%;
    background-color: blue;
    align-self: center;
    justify-content: center;
    align-items: center;
  }
  
  .start-option-list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 82%;
    height: 100%;
  }

  .start-option {
    width: 100%;
    display: flex;
  }
  
  .start-option-container {
    display: flex;
    justify-content: center;
    width: 99.25%;
    margin: 1.5% 0;
  }

  #shutdown {
    margin: 0;
  }

  .start-option-arrow {
    font-size: .75em;
    margin: 5%;
    align-self: center;
  }
  
  .start-title {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    color: white;
    font-weight: bold;
    font-family: Cambria;
    font-size: 2em;
    align-self: flex-end;
    margin: 15% 0;
  }
  
  .start-item {
    display: flex;
    text-align: center;
    align-self: center;
    list-style-type: none;
    padding: 0 10%;
  }
  
  .start-option-container:hover {
    background-color: blue;
    color: white;
    cursor: pointer;
  }
  
  .min-win {
    display: flex;
    justify-content: flex-start;
  }
  
  .min-win-button {
    cursor: pointer;
    display: flex;
    width: fit-content;
    height: 100%;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    border-radius: 2px;
    padding-inline: 7px;
  }
  
  .selected {
    font-weight: bold;
    background: rgb(172,172,172);
    background: linear-gradient(180deg, rgba(172,172,172,1) 0%, rgba(205,205,205,1) 18%);
  }
  
  .start-item-icon {
    margin: 0 5%;
    width: 40px;
  }
